h1,
h2,
h3,
h4,
h5,
h6 {
  margin-block-start: 0px;
  margin-block-end: 0px;
}
.App {
  text-align: center;
}

.TAL {
  text-align: center;
}

.LMA {
  margin-top: -20px;
}

.MAIN {
  width: 100%;
  height: 88vh;
  max-width: 360px;
  margin: 7px auto;
  padding: 5px;
  overflow-x: scroll;
  overflow-y: scroll;
}

.CONTMAIN {
  border-radius: 16px;
  height: auto;
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: auto;
  margin-right: auto;
  /* background-color: #666666; */
  -webkit-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
  overflow: hidden;
}

.PrPho {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin: 1px auto;
  text-align: center;
  -webkit-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
}

.PrPho img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}

.PrNam {
  /* background-color: red; */
}

.PrCon {
  text-align: center;
}

.PrCon > span:first-child {
  margin: 0px 16px;
}
.PrCon > span:last-child {
  margin: 0px 16px;
}

.SKISCR {
  overflow-x: scroll;
}

.SKISHOBOARD span {
  display: inline-block;
  margin: 3px 10px;
  padding: 0.5%;
  -webkit-box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.75);
  border-radius: 9px 9px 9px 9px;
}

.SKISHOBOARD span > div {
  display: block;
  text-align: center;
}

.SKIIMG {
  width: 100px;
  height: 100px;
  margin: auto;
}

.SKINAM {
  margin-top: 7px;
}
